@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap);
* {
  font-family: "Open Sans", sans-serif;
}
html {
  zoom: 0.9;
}
/* .MuiFormControl-root{
  width: 100%;
  min-width: initial;
} */
body {
  margin: 0;
  font-family: "Josefin Sans", sans-serif;
  /* font-family: 'Open Sans', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 17px;
  padding: 0;
  box-sizing: border-box;
  -webkit-animation: fadeIn 1s ease-in-out;
          animation: fadeIn 1s ease-in-out;
}
.recycle12 {
  width: 100%;
  margin-top: 10px;
  background-color: rgb(250, 57, 57);
  color: white;
  width: 100px;
  border-radius: 12px;
  padding: 2px;
  text-align: center;
}
.SelectedCountries {
  width: 100%;
  height: 40px;
  margin-top: 4px;
}

.dropdown-content-flag.show {
  z-index: 999;
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
::-webkit-scrollbar {
  display: none;
}
a {
  text-decoration: none;
  color: black;
}
a:hover {
  color: black;
}
i,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span {
  padding: 0;
  margin: 0;
}
input:focus,
select:focus,
textarea:focus {
  outline: 0px;
}
.row,
.col-6,
.col-4 {
  margin: 0;
  padding: 0;
}
.Announcement {
  background-color: #3471db;
  padding: 10px 0px 6px 0px;
  position: relative;
  transition: all 1s ease-in-out;
}

.Announcement .col-md-6 p {
  color: white;
  margin-right: 6rem;
  font-size: 15px;
}

.Announcement .col-12 a {
  color: white;
  margin-right: 2rem;
  font-size: 15px;
}

/* Header */
.header {
  padding: 10px 0px 10px 0px;
  box-shadow: 0 2px 4px rgba(44, 42, 42, 0.3);
  height: 13vh;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: white;
  margin-bottom: 100px;
}

.fresh-recom {
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: 1.75rem;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.75);
}

.navbar-brand img {
  width: 60%;
}
.input-group {
  height: 50px;
  width: 90%;
  /* border: 2px solid red; */
}

.search-button {
  width: 20%;
  background-color: #3498db;
  color: white;
  border: 0;
  text-transform: uppercase;
  font-size: 12px;
  margin-top: 5px;
  height: 40px;
}

.Login-Register a {
  text-transform: uppercase;
  font-size: 15px;
  margin-right: 3.4rem;
  position: relative;
}
.Login-Register a:nth-last-child(1) {
  margin-right: 0rem;
}

.loginButton {
  padding: 7px;
  border-radius: 50px;
  text-align: center;
  font-weight: bold;
  box-sizing: border-box;
  transition: 0.2s all ease-in;
  width: 100px;
  font-size: 18px;
  text-transform: capitalize;
  margin-left: 10px;
  color: rgba(0, 0, 0, 0.7);
  font-weight: bold;
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
@media screen and (min-width: 992px) and (max-width: 1200px) {
  .Login-Register a {
    margin-right: 2rem;
  }
  /* .searchBox {
    border: 2px solid red !important;
    display: none !important;
    position: absolute;
    right: 48%;
    top: 3.6%;
    width: 100px !important;
  } */
}

@media only screen and (min-width: 821px) and (max-width: 861px) {
  .loginButton {
    margin-right: -50px;
  }
}

.searchInput:focus::-webkit-input-placeholder {
  color: transparent;
}

.searchInput:focus::placeholder {
  color: transparent;
}
.loginButton:hover {
  padding: 7px;
  border-radius: 50px;
  text-align: center;
  color: white;
  font-weight: bold;
  background-color: #3498db;
  box-sizing: border-box;
  border: 1px solid #3498db;
  transition: 0.2s all ease-in;

  text-transform: capitalize;
  margin-left: 10px;
}

.sellProduct {
  padding: 7px;
  border-radius: 50px;
  text-align: center;
  color: white;
  font-weight: bold;
  background-color: #3498db;
  box-sizing: border-box;
  border: 1px solid #3498db;
  transition: 0.2s all ease-in;
  width: 100px;
  font-size: 18px;
  text-transform: capitalize;
  margin-left: 10px;
}
.bi.bi-cart-check {
  transition: 0.4s;
  color: rgba(0, 0, 0, 0.8);
}

.bi.bi-cart-check:hover {
  fill: #3498db;
}
.sellProduct:hover {
  color: rgba(0, 0, 0, 0.7);
  font-weight: bold;
  border: 1px solid rgba(0, 0, 0, 0.5);
  background-color: white;
}

.badge {
  background-color: red;
  position: absolute;
  height: 1.3rem;
  width: 1.3rem;
  border-radius: 50%;
  text-align: center;
  font-size: 13px;
  left: 82%;
  top: -45%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.containflag {
  display: flex;
  justify-content: center;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 50px;
  padding: 0px;
  margin-right: 4.7rem;
}
.dropdown-flag {
  position: relative;
  display: inline-block;
}
.fa-whatsapp {
  color: white;
}
.wp img {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 0.1rem;
}
.wp {
  float: right;
  z-index: 999999999999999999999999999999999999999;
  border: 2px solid rgb(67, 240, 76);
  background-color: #50d860;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  cursor: pointer;
  margin-right: 15px;
}
.contain-wp {
  width: 10%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  /* border: 2px solid red; */
  position: fixed;
  top: 85%;
  left: 90%;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.product-detail-image {
  width: 400px;
  height: 400px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.small-images-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 40px;
}
.small-image {
  border-radius: 8px;
  background-color: #ebebeb;
  width: 70px;
  height: 70px;
  cursor: pointer;
}
.small-image img {
  width: 50px;
  height: 50px;
}
.selected-image {
  background-color: #f02d34;
}
.wp::before {
  content: "Do You need any Help?  Message us.";
  display: block;
  position: absolute;
  top: -1;
  right: 55%;
  width: 200px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 10px;
  color: wheat;
  background-color: #455a64;
  z-index: -1;
  opacity: 0;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 5px;
  transition: opacity 0.3s ease-in-out;
}

.wp:hover::before {
  opacity: 1;
}
.dropdown-content-flag {
  display: none;
  position: absolute;
  right: 1%;
  z-index: 1;
  background-color: #fff;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 8px;
  border-radius: 5px;
  margin-top: 5px;
}

.dropdown-content-flag a {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 8px;
  text-decoration: none;
  grid-gap: 7px;
  gap: 7px;
  margin-bottom: 5px;
}

.dropdown-content-flag a:hover {
  background-color: #f1f1f1;
}

.dropdown-content-flag.show {
  display: block;
}
.name-button {
  padding: 5px 15px;
  border: 1px solid #e4e4e4;
  background-color: white;
  border-radius: 2px;
  margin: 0 25px;
}

.dropdown-menu {
  background-color: rgb(255, 255, 255) !important;
  border: 0 !important;
  padding: 0 !important;
  overflow: hidden;
}
.dropdown-menu a {
  padding: 10px 20px !important;
  color: #000000 !important;
  margin: 0 !important;
}
.dropdown-menu a:hover {
  color: #3498db !important;
  background-color: whitesmoke !important;
}
.dropdown-menu a:visited {
  background-color: white;
}
.mobile-header {
  display: none;
}
.container {
  min-width: 100px !important;
}
/* SHOP */
.section {
  width: 100%;
  padding-bottom: 6%;
  padding-top: 2%;
}
.sidebar_item {
  margin-bottom: 35px;
}
.sidebar_item h4 {
  color: #1c1c1c;
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 25px;
}
.sidebar_item ul {
  margin: 0;
  padding: 0;
}
.sidebar_item ul li {
  list-style: none;
  display: flex;
}

.sidebar_item ul li a {
  font-size: 16px;
  color: #000000;
  line-height: 45px;
  display: block;
  font-family: "Open Sans", sans-serif;
}
.sidebar_item ul li img {
  width: 25px;
  object-fit: contain;
  margin-left: 20px;
}
.shop {
  margin-bottom: 50px;
}
.border-product {
  padding: 15px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}
.shopBack {
  height: 250px;
  position: relative;
  overflow: hidden;
  width: 100%;
  background-color: #f3fbf7;
}
.shopBack img {
  width: 100%;
  height: 250px;
  object-fit: contain;
}
.shoptext {
  padding-top: 10px;
}
.shoptext p a {
  color: #252525;
}
.shoptext h3 {
  color: #252525;
  margin-top: 13px;
  font-weight: bold;
  font-size: 19px;
}
.rating {
  margin: 5px 0;
}
.rating i {
  font-size: 12px;
  color: #ebb450;
}
.rating span {
  margin-left: 10px;
}
/* END OF SHOP */

