.slider {
  width: 70%;
  margin: auto;
  margin-top: 50px;
  border-radius: 12px;
  height: 500px;
  overflow: hidden;
  position: relative;
}

.slides {
  width: 500%;
  height: 70%;
  display: flex;
}
.slides::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.7));
  z-index: 2;
}

.slides input {
  display: none;
}

.slide {
  width: 20%;
  transition: 2s;
}
.slide img {
  width: 100%;
  height: 500px;
}

.navigation-manual {
  position: absolute;
  width: 100%;
  /* border: 2px solid red; */
  bottom: 20px;
  display: flex;
  justify-content: center;
  z-index: 10;
}

.manual-btn {
  border: 2px solid white;
  padding: 5px;
  border-radius: 10px;
  cursor: pointer;
  transition: 1s;
}
.manual-btn:not(:last-child) {
  margin-right: 40px;
}

.manual-btn:hover {
  background: #3498db;
}

