@media (max-width: 1020px) {
  .wp::before {
    margin-right: 20px;
  }
}
@media (max-width: 992px) {
  .display-none {
    display: none !important;
  }
  .containflag {
    margin-right: 2rem;
  }
  .searchButton {
    margin-left: -70px;
  }
  .searchBox {
    width: 280px !important;
  }
  .Login-Register a {
    font-size: 10px;
  }
  .btn-group button {
    font-size: 13px;
  }
  .badge {
    top: -13px;
    padding: 5px;
    font-size: 10px;
  }
  .input-group {
    height: 30px;
  }
  .single-image,
  .single-image img {
    height: 400px;
  }
  .author-card-avatar img {
    margin-top: -20%;
  }
  .order-box {
    width: 50px;
    height: 50px;
    font-size: 15px;
  }
  .order-detail p {
    font-size: 12px;
  }
  .order-detail {
    padding: 20px 0;
  }
  .order-detail h5 {
    font-size: 13px;
  }
  .subtotal-order button {
    margin-bottom: 30px;
  }
  .single-product {
    margin-top: 20px;
  }
}
@media (max-width: 908px) {
  .searchBox {
    width: 290px !important;
    height: 35px;
  }
  .searchButton {
    margin-left: -130px;
    height: 35px;
  }
  .searchInput::placeholder {
    font-size: 13px;
  }
  .loginButton {
    width: 70px;
    font-size: 13px;
  }
  .sellProduct {
    width: 70px;
    font-size: 13px;
  }
}

@media (max-width: 822px) {
  .searchBox {
    width: 220px !important;
  }
  .searchButton {
    margin-left: -125px;
  }
  .searchInput::placeholder {
    font-size: 14px;
  }
}
@media all and (max-width: 520px) {
  .loginButton {
    width: 50px;
    font-size: 12px;
  }
  .sellProduct {
    width: 50px;
    font-size: 12px;
    margin-left: -30px;
  }
  .navbar-brand img {
    margin-left: -20px;
    width: auto;
    height: auto;
  }
  .containflag {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: -30px;
  }
  .containflag a {
    margin-right: 30px;
  }
  .containflag div {
    width: fit-content;
    height: fit-content;
  }
  .badge {
    height: fit-content;
    width: fit-content;
  }
}

@media (max-width: 768px) {
  .pc-header {
    display: none !important;
  }
  .wp::before {
    margin-right: 20px;
  }
  .mobile-header {
    display: block;
  }
  .cart-mobile-icon i {
    font-size: 13px;
    margin-left: 10px;
  }
  .btn-group button {
    font-size: 12px;
    width: 70px;
  }
  .mobile-header img {
    width: 60px;
  }
  .input-group {
    width: 100%;
    margin-top: 5px;
  }

  .subscribe-head .form-section input[type="email"] {
    height: 45px;
    width: 70%;
  }
  .subscribe-head .form-section input[type="submit"] {
    height: 47px;
    padding: 0 58px;
  }
  .subscribe-head h2 {
    font-size: 25px;
  }
  .contact-Box {
    margin: 30px 0;
  }
  .contactInfo {
    padding: 20px 0;
  }
  .cart-buttons {
    margin: 40px 0;
  }
  .cart-buttons a button,
  .cart-buttons .d-flex button {
    width: 100%;
  }
  .total .sub,
  .total .total-price {
    font-size: 16px;
  }

  .single-image {
    margin-bottom: 50px;
    margin-right: 0;
  }
  .product-name {
    font-size: 23px;
  }
  .author-card-avatar {
    display: none;
  }
  .order-detail {
    margin-top: 20px;
  }
}
@media (max-width: 660px) {
  .wp::before {
    margin-right: 40px;
  }
}

@media (max-width: 575.98px) {
  .subscribe-head .form-section input[type="email"] {
    width: 100%;
  }
  .social__icons {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .wp::before {
    margin-right: 20px;
  }
  .subscribe-head .form-section input[type="submit"] {
    width: 100%;
    padding: 0 0;
    margin-top: 20px;
    margin-left: 0px;
  }
  .subscribe-head h2 {
    font-size: 25px;
  }
  .subscribe-section {
    padding: 70px 0 70px;
  }
  .card-name {
    width: 8%;
    margin: 0 7px;
  }
  .login-center {
    padding: 20px 0;
  }
  .center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .center h5 {
    font-size: 17px;
    margin-top: 10px;
  }
  .center p {
    font-size: 17px;
  }
}
@media (max-width: 570px) {
  .wp::before {
    margin-right: 40px;
  }
}

@media (max-width: 440px) {
  .subscribe-head h2 {
    font-size: 20px;
  }

  .loginButton {
    width: 70px;
  }
  .sellProduct {
    width: 70px;
    margin-right: -20px;
  }
  .navbar-brand {
    margin-left: 15px;
  }
  .containflag {
    margin-right: -17px;
  }
  .single-image,
  .single-image img {
    height: 200px;
  }
  .badge {
    font-size: 8px;
  }
  .wp::before {
    margin-right: 50px;
  }
  .social__icons {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
